angular.module("app").component("userDetails", {
  template: require('./userDetails.html'),
  bindings: {
    allUsers: "="
  },
  controller: function($routeParams) {
    this.user = this.allUsers.find(function(user) {
      return user.id === parseInt($routeParams.id);
    });
  }
});



