angular.module("app").component("unreviewedTalk", {
  template: require('./unreviewedTalk.html'),
  bindings: {
    session: "=",
    voteYes: "&",
    voteNo: "&"
  },
  controller: function() {
    this.yes = function() {
      this.voteYes();
    };

    this.no = function() {
      this.voteNo();
    };
  }
});



